@import url(https://fonts.googleapis.com/css?family=Lato:300,700&display=swap);
.carousel-caption {
  width: 300px;
  height: 100px;
  padding: 20px;

  position: absolute;
  top: 50%;
  left: 50%;

  margin: -70px 0 0 -170px;
  color: #ccccb8;

  font-family: "Roboto", sans-serif;
}

.carousel-caption h3 {
  font-weight: 900;
  color: white;
  text-transform: uppercase;
  text-shadow: 7px 6px 7px #000000;
}

.carousel-caption p {
  color: black;

  text-shadow: 0 1px 0 #cccccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9,
    0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1),
    0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2),
    0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2),
    0 20px 20px rgba(0, 0, 0, 0.15);

  text-shadow: 0 0 10px #ffffff;
  font: 30px Monaco, Mono-Space;
}

.d-block {
  width: 100%;
  height: 100vh;
}

.fa-sort {
  display: none;
}

th:hover .fa-sort {
  display: inline;
}

.editButton {
  display: none;
}

td:hover .editButton {
  display: inline-block;
  margin-left: 3px;
}

.grantButton{
  margin-left: 3px;
}

.deleteButton{
  display:none
}

tr:hover .deleteButton{
  display: inline-block;
  margin-left: 3px;
}



th span.sort-by { 
	padding-right: 18px;
	position: relative;
}
span.sort-by:before,
span.sort-by:after {
	border: 4px solid transparent;
	content: "";
	display: none;
	height: 0;
	right: 5px;
	top: 50%;
	position: absolute;
	width: 0;
}
span.sort-by:before {
	border-bottom-color: #666;
	margin-top: -9px;
}
span.sort-by:after {
	border-top-color: #666;
	margin-top: 1px;
}

.sort-by-parent:hover 
span.sort-by:before
{
  display: block;
}

.sort-by-parent:hover 
span.sort-by:after
{
  display: block;
}

#cc{
  width: 150px;
}

.statusFilterForm{
  margin-top: 20px;
}

